/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


*{
    margin: 0;
    padding:0;
}

$modal-color: rgba(233, 192, 192,0.95);
$lower-backgroundcolor: rgb(228, 228, 70);
$text-color: rgb(0,0,0);
$clikcObj-container:rgba(221, 220, 220,0.3);

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    box-sizing: border-box;
    background: $lower-backgroundcolor;
    color: $text-color;
  }
  
@font-face {font-family: "Pokemon"; src: url("//db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.eot"); src: url("//db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.woff") format("woff"), url("//db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.svg#Pokemon") format("svg"); }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  


.open-modal-container{
    position: absolute;
     display: flex;
     width: 100%;
     height: 100%;
     padding-bottom: 15%;
     background: rgba(155,155,0,0.4) fixed;
     justify-content: center;
     align-items:center ;
         
}

.open-modal{
    background: $modal-color;
    height: 500px;
    width: 500px;
    position: absolute;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    left:37%;
    top:20%;
    flex-flow: column nowrap;
    border: 2px solid black;
    border-radius: 10%;
    z-index: 2;


    h2{
        padding:16px 16px 32px 16px;
    }

    .modal-text{
        margin: 16px;
        font-size:4vh;
        font-weight: 300;
    }

    hr{
        height: 2px;
        width: 500px;
        background-color: rgba(155,0,0,0.9);
    }

    button{
        padding:16px;
        font-size:2vh ;
    }
}


.header{
  color: $text-color;
  background: rgb(238, 64, 64);
  width: 100%;
  height: 3em;
}

.line{
  background:black;
  height: 20px;
  width: 100%;
  display:flex;
  justify-content:center ;
  align-items: center;


  .layer1{
    display:flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    width: 60px;
    border: 6px solid #333333;
    background:white; 
    border-radius: 50%;


    .layer2{
        display:flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        width: 40px;
        border: 4px solid #333333;
        background:white;
        border-radius: 50%;


        .layer3{
            display:flex;
            align-items: center;
            justify-content: center;
            height: 10px;
            width: 10px;
            border: 1px solid #333333;
            background:black ;
            border-radius: 50%;
          }
      }
  }


}

.lower-margin{
  margin-bottom:32px;
}


.App{
  width: 900px;
  height: 840px;
  display:flex;
  flex-flow: row wrap;
  background:$clikcObj-container;
  justify-content: space-around;
  align-items: center;
  gap: 12px;
  border-radius: 2%;
}


.clikcObj{
  display:flex;
  flex-flow: column nowrap;
  width: 200px;
  height: 150px;
  background: rgba(158, 99, 99, 0.1);
  text-align: center;
  border: 1px solid white;
  border-radius: 10%;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.7);
  transition: transform 1s;

}

.clikcObj:hover{
  transform: scale(1.1);
}

.center{
  display:flex;
  justify-content:center;
  align-items: center;
  text-align: center;
}

.text-change{
  font-family:"Nunito", sans-serif;
  font-size: 24px;
  font-weight: 500;
  color: $text-color;

}

.score-display{
padding: auto 32px;
  align-self: center;
  flex-flow: column nowrap;
  justify-content: flex-start;
  row-gap:32px ;
}


.poke-font{
  font-size:64px;
  font-family: 'Pokemon';
}

.main-controller{
  column-gap: 128px;
}

.align-pos{
align-self: flex-start;
text-align: center;
margin-bottom: 32px;
}

.img-container{
  width: 150px;
  height: 150px;
}

